<template>
    <div class="admin-registration">
        <h1 class="h2 centered">Admin Registration</h1>
        <div class="login-panel">
           <div class="logo-container">
               <logo class="logo"></logo>
           </div>
           <form>
                <div class="form-group form-focus">
                    <label class="focus-label">Email</label>
                    <input class="form-control" v-model="email" type="text">
                </div>
                <div class="form-group form-focus">
                    <label class="focus-label">Password</label>
                    <input class="form-control" v-model="password" type="password">
                </div>
                <div class="form-group text-center">
                    <button v-if="!loading" @click="registerAccount" class="btn btn-success btn-block account-btn" type="submit">
                        Register
                    </button>
                    <button v-else class="btn btn-success btn-block account-btn" type="button" disabled>
                        <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                        Loading...
                    </button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>

    import firebase from 'firebase'

    import Logo from './Logo'

    export default {
        name: "admin-registration",
        components: {
            'logo': Logo
        },
        data() {
            return {
                email: "",
                password: "",

                loading: false,

                name: "Super Administrator"
            }
        },

        methods: {
            registerAccount(e) {
                this.loading = true
                e.preventDefault()
                firebase
                    .auth()
                    .createUserWithEmailAndPassword(this.email, this.password)
                    .then( response => {
                        this.loading = false
                        alert(`Account created for ${this.name}`)
                        this.$router.go({ path: this.$router.path })
                    },
                    
                    err => {
                        this.loading = false
                        alert(err.message)
                    })
            }
        }
    }
</script>

<style scoped>
    .admin-registration {
        width: 100%;
        margin-top: 70px;
    }

    .login-panel {
        max-width: 400px;
        margin: 0 auto;
        padding: 30px 30px 60px;

        background: #000;
        border: 1px solid #333
    }

    .logo-container {
        width: 100%;
        margin: 30px 0;
    }
</style>


