import Vue from 'vue'
import Router from 'vue-router'

import Dashboard from '../components/Dashboard'
import Tutorials from '../components/Tutorials'
import Tutors from '../components/Tutors'
import Users from '../components/Users'
import Login from '../components/Login'
import Register from '../components/Register'
import ReviewTutorPage from '../components/ReviewTutorPage'

Vue.use(Router)

const router = new Router({
    routes: [
        {
            path: '/',
            name: 'admin-dashboard',
            component: Dashboard,
            meta: {
              requiresAuth: true
            }
        },{
            path: '/tutorials',
            name: 'tutorials',
            component: Tutorials,
            meta: {
              requiresAuth: true
            }
        },{
            path: '/users',
            name: 'users',
            component: Users,
            meta: {
              requiresAuth: true
            }
        },{
            path: '/tutors',
            name: 'tutors',
            component: Tutors,
            meta: {
              requiresAuth: true
            }
        },{
          path: '/tutors/:user_id',
          name: 'review-tutor',
          component: ReviewTutorPage,
          meta: {
            requiresAuth: true
          }
      },{
            path: '/admin-register',
            name: 'register',
            component: Register,
            meta: {
				requiresVisitor: true
            }
        },{
            path: '/admin-login',
            name: 'login',
            component: Login,
            meta: {
				requiresVisitor: true
            }
        }
    ]
})


export default router



