<template>
    <div class="admin-dashbord">
        <div class="page-wrapper" style="min-height: 747px;">
             <div class="content container" style="position: relative;">
                <div class="water-mark" style="position: absolute;">{{getStatusName}}</div>
                <div class="row">
                    <div class="col-sm-12">
                        <h4 class="page-title">Tutor Account Review</h4>
                        <div class="row">
                            <div class="col-md-6">
                                <ul class="list-group list-group-flush">
                                    <li class="list-group-item transparent-bg">&nbsp;Account Holder : <strong class="text-light" style="padding-left: 30px;">{{firstname}} {{lastname}}</strong></li>
                                    <li class="list-group-item transparent-bg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Email : <strong class="text-light" style="padding-left: 30px;"> {{email}}</strong></li>
                                    <li class="list-group-item transparent-bg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Mobile : <strong class="text-light" style="padding-left: 30px">{{mobile}}</strong></li>
                                    <li class="list-group-item transparent-bg">Account Status : 
                                        <strong class="text-light" style="padding-left: 30px;">
                                            <span v-if="status === 3" class="badge badge-secondary text-capitalize">{{getStatusName}}</span>
                                            <span v-if="status === 1" class="badge badge-success text-capitalize">{{getStatusName}}</span>
                                            <span v-if="status === 4" class="badge badge-danger text-capitalize">{{getStatusName}}</span>
                                            <span v-if="status === 2" class="badge badge-warning text-capitalize">{{getStatusName}}</span>
                                        </strong>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row" v-if="pendingStatus">
                    <!-- Review Pending submissions -->
                    <h3 class="col-12 h3 mt-5 d-flex justify-content-center">Review Tutor Application For Approval</h3>

                    <div class="list-group col-10 offset-1">
                        <span class="list-group-item list-group-item-action  transparent-bg d-flex justify-content-center">
                            <h3 class="h4">Verification Links for Github and LinkedIn profile</h3>
                        </span>
                        <span class="text-light list-group-item list-group-item-action transparent-bg d-flex justify-content-center">
                            <p>Please use the links below to verify the profile of the prospective tutor on this platform</p>
                        </span>
                        <span class="text-light list-group-item list-group-item-action transparent-bg" style="display: flex; align-items: center; justify-content: center;">
                            <i class="fab fa-github fa-3x"></i>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Click &nbsp;<a :href="github_link">here to review</a> &nbsp; Github Profile
                        </span>
                        <span class="text-light list-group-item list-group-item-action transparent-bg" style="display: flex; align-items: center; justify-content: center;">
                            Click &nbsp;<a :href="github_link">here to review</a> &nbsp; LinkedIn Profile&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i class="fab fa-linkedin fa-3x"></i>
                        </span>
                    </div>

                    <div class="col-12 mt-3" style="text-align: center;" v-if="!showRejectionReason">
                        <button @click="initiateReject" :disabled="loading" class="btn btn-lg btn-danger mr-5">Reject Application</button>
                        <button v-if="!loading" @click="approveApplication" :disabled="showRejectionReason" class="btn btn-lg btn-success">Approve Application</button>
                        <button v-else class="btn btn-lg btn-success" type="button" disabled>
                            <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                            Loading...
                        </button>
                    </div>

                    <div class="col-12 mt-3" style="text-align: center;" v-if="showRejectionReason">
                        <form>
                            <div class="form-group col-10 offset-1">
                                <label>Reason for Rejection</label>
                                <textarea v-model="message" class="form-control" rows="3"></textarea>
                            </div>
                            <div class="col-12 mt-3" style="text-align: center;">
                                <button @click="cancelReject" class="btn btn-lg btn-secondary mr-5">Cancel</button>
                                <button v-if="!loading" @click="rejectApplication" class="btn btn-lg btn-danger">Submit</button>
                                <button v-else class="btn btn-lg btn-danger" type="button" disabled>
                                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                    Loading...
                                </button>
                            </div>
                        </form>
                    </div>
                </div>


                <div class="row" v-if="approvedStatus">
                    <!-- Review Approved submissions -->
                    <h3 class="col-12 h3 mt-5 d-flex justify-content-center">No Action Required</h3>

                    <div class="list-group col-10 offset-1">
                        <span class="list-group-item list-group-item-action  transparent-bg d-flex justify-content-center" style="display: flex; align-items: center; justify-content: center;">
                            <h3 class="h4"><a href="#" class="badge badge-success"><i class="fas fa-check-circle fa-3x"></i></a></h3>
                        </span>
                        <span class="text-light list-group-item list-group-item-action transparent-bg" style="display: flex; align-items: center; justify-content: center;">
                            
                        </span>
                    </div>

                    <h3 class="col-12 h3 mt-5 d-flex justify-content-center" v-if="!showSuspendAccount">Review Account Approval Status</h3>
                    <h3 class="col-12 h3 mt-5 d-flex justify-content-center" v-if="showSuspendAccount">Suspend Tutor Account</h3>

                    <div class="col-12 mt-3" style="text-align: center;" v-if="!showSuspendAccount">
                        <button @click="initiateSuspend" :disabled="loading" class="btn btn-lg btn-warning">Review Options</button>
                    </div>

                    <div class="col-12 mt-3" style="text-align: center;" v-if="showSuspendAccount">
                        <form>
                            <div class="form-group col-10 offset-1">
                                <label>Reason for Suspension</label>
                                <textarea v-model="message" class="form-control" rows="3"></textarea>
                            </div>
                            <div class="col-12 mt-3" style="text-align: center;">
                                <button @click="cancelSuspend" class="btn btn-lg btn-secondary mr-5">Cancel</button>
                                <button v-if="!loading" @click="suspendAccount" class="btn btn-lg btn-danger">Suspend Account</button>
                                <button v-else class="btn btn-lg btn-danger" type="button" disabled>
                                    <span class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                                    Loading...
                                </button>
                            </div>
                        </form>
                    </div>
                </div>

                <div class="row" v-if="suspendedStatus">
                    <!-- Review Suspened submissions -->
                </div>

                <div class="row" v-if="rejectedStatus">
                    <!-- Review Reject submissions -->
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import datastore from '../datastore/firebaseInit'

    import Header from './Header'
    import SideBar from './SideBar'

    export default {
        name: "admin-dashbord",
        components: {
            'dashboard-header': Header,
            'dashboard-sidebar': SideBar
        },
        data() {
            return {
                activePage: 'Tutors/Review',
                firstname: "",
                lastname: "",
                email: "",
                mobile: "",
                status: "",
                user_id: "",
                github_link: "",
                linkedin_link: "",
                message: "",

                approvalStatus: [],

                showRejectionReason: false,
                showSuspendAccount: false,

                loading: false
            }
        },

        beforeRouteEnter(to, from, next) {
            datastore.collection("app_tutors").where('user_id', '==', to.params.user_id).get()
                .then( querySnapshot => {
                    querySnapshot.forEach( doc => {
                        next(vm => {
                            vm.user_id = doc.data().user_id
                            vm.firstname = doc.data().firstname
                            vm.lastname = doc.data().lastname
                            vm.email = doc.data().email
                            vm.mobile = doc.data().mobile
                            vm.status = doc.data().approval_status
                            vm.github_link = doc.data().github_link
                            vm.linkedin_link = doc.data().linkedin_link
                        })
                    })

                })
        }, 

        watch: {
            '$route': 'fetchData'
        }, 

        computed: {
            getStatusName() {
                let selected = null;
                this.approvalStatus.map((statusInstance) => {
                    if (statusInstance.status_id === this.status) {
                        selected = statusInstance
                    }
                })

                if (selected) {
                    return selected.name
                }
                return "Unknown Status"
            },

            pendingStatus() {
                return this.status === 2
            },

            approvedStatus() {
                return this.status === 1
            },

            suspendedStatus() {
                return this.status === 3
            },

            rejectedStatus() {
                return this.status === 4
            }
        },

        methods: {
            fetchData() {
                datastore.collection("app_tutors").where('user_id', '==', this.$route.params.user_id).get()
                .then( querySnapshot => {
                    querySnapshot.forEach( doc => {
                        this.firstname = doc.data().firstname
                        this.lastname = doc.data().lastname
                        this.email = doc.data().email
                        this.mobile = doc.data().mobile
                        this.status = doc.data().approval_status
                        this.user_id = doc.data().user_id
                        this.github_link = doc.data().github_link
                        this.linkedin_link = doc.data().linkedin_link
                    })
                })
            },

            initiateReject() {
                this.showRejectionReason = true
            },

            approveApplication() {
                if (confirm("Do you want to approve this application?")) {
                    this.loading = true
                    datastore.collection("app_tutors").where('user_id', '==', this.$route.params.user_id).get()
                        .then( querySnapshot => {
                            querySnapshot.forEach( doc => {
                                doc.ref.update({
                                    "approval_status": 1,
                                }).then(() => {
                                    alert("Application Approval was Successful.")
                                    this.$router.push("/tutors")
                                    this.loading = false
                                })
                            })
                        })
                }
            },

            cancelReject() {
                this.showRejectionReason = false
            },

            rejectApplication() {
                this.loading = true

                if (!this.message) {
                    alert("Please add Rejection Message!!!")
                    this.loading = false
                    this.$router.push({
                        name: "review-tutor",
                        params: {user_id: this.user_id}
                    })
                } else {
                     if (confirm("Do you want to reject this application?")) {
                        datastore.collection("app_tutors").where('user_id', '==', this.$route.params.user_id).get()
                        .then( querySnapshot => {
                            querySnapshot.forEach( doc => {
                                doc.ref.update({
                                    "approval_status": 4,
                                    "message": this.message
                                }).then(() => {
                                    alert("Application has been Rejected Successful.")
                                    this.$router.push("/tutors")
                                    this.loading = false
                                })
                            })
                        })
                    }
                }
            },

            initiateSuspend() {
                this.showSuspendAccount = true
            },

            cancelSuspend() {
                this.showSuspendAccount = false
            },

            suspendAccount() {
                if (!this.message) {
                    alert("Please add Suspension Message!!!")
                    this.loading = false
                    this.$router.push({
                        name: "review-tutor",
                        params: {user_id: this.user_id}
                    })
                } else {
                     if (confirm("Do you want to suspend this user Account?")) {
                        datastore.collection("app_tutors").where('user_id', '==', this.$route.params.user_id).get()
                        .then( querySnapshot => {
                            querySnapshot.forEach( doc => {
                                doc.ref.update({
                                    "approval_status": 3,
                                    "message": this.message
                                }).then(() => {
                                    alert("User Account has been Suspended Successful.")
                                    this.$router.push("/tutors")
                                    this.loading = false
                                })
                            })
                        })
                    }
                }
            }
        },

        created() {
            datastore.collection("account_status").orderBy("name").get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        "id": doc.id,
                        "name":  doc.data().name,
                        "status_id": doc.data().status_id
                    }
                    this.approvalStatus.push(data)
                })
            }) 
        }
    }
</script>

<style scoped>
    .page-wrapper {left: 0;margin-left: 220px;padding-top: 30px;position: relative;-webkit-transition: all 0.4s ease;-moz-transition: all 0.4s ease;transition: all 0.4s ease;}
    .page-wrapper > .content{padding:30px;}

    .transparent-bg { background: transparent;}
    .water-mark {
        width: 100%;
        height: 60vh;

        display: flex;
        align-items: center;
        justify-content: center;

        font-size: 150px;
        color: rgba(0, 0, 0, 0.2);

        transform: rotate(315deg);
    }

    @media only screen and (max-width: 991.98px) {
        .slide-nav .page-wrapper {left: 225px;margin-left: 0;}
        .page-wrapper {margin-left: 0;padding-left: 0;padding-right: 0;}
        .slide-nav .page-wrapper {left: 225px;margin-left: 0;}
        body {font-size:13px;}
    }

    @media only screen and (max-width: 575.98px) {
        .page-wrapper > .content {padding: 15px;}
        body {font-size:12px;}
    }
</style>


