<template>
    <div class="sidebar" id="sidebar">
        <div class="slimScrollDiv" style="position: relative; overflow: hidden; width: 100%; height: 687px;"><div class="sidebar-inner slimscroll" style="overflow: hidden; width: 100%; height: 687px;">
            <div id="sidebar-menu" class="sidebar-menu">
                <ul>
                    <li class="menu-title">Main</li>
                    <li :class="{ active: isDashboardActive }">
                        <router-link to="/"><i class="fa fa-tachometer-alt"></i> Dashboard</router-link>
                    </li>
                    <li @click="toggleSubMenu($event, 'users')" class="submenu">
                        <a href="#">
                            <i class="fas fa-users" aria-hidden="true"></i> 
                            <span>Users</span> 
                            <i v-if="!toggleUsers" class="fas fa-chevron-right position-right"></i>
                            <i v-if="toggleUsers" class="fas fa-chevron-down position-right"></i>
                        </a>
                        <ul class="list-unstyled" :class="{ 'collapsed': !toggleUsers, 'expanded': toggleUsers }">
                            <li :class="{ active: isUserActive }"><router-link to="/users"><i class="fas fa-level-up-alt icon-rotate"></i>&nbsp;&nbsp;All Users</router-link></li>
                        </ul>
                    </li>

                    <li @click="toggleSubMenu($event, 'tutors')" class="submenu">
                        <a href="#">
                            <i class="fas fa-users" aria-hidden="true"></i> 
                            <span>Tutors</span> 
                            <i v-if="!toggleTutors" class="fas fa-chevron-right position-right"></i>
                            <i v-if="toggleTutors" class="fas fa-chevron-down position-right"></i>
                        </a>
                        <ul class="list-unstyled" :class="{ 'collapsed': !toggleTutors, 'expanded': toggleTutors }">
                            <li :class="{ active: isTutorActive }"><router-link to="/tutors"><i class="fas fa-level-up-alt icon-rotate"></i>&nbsp;&nbsp;All Tutors</router-link></li>
                        </ul>
                    </li>

                    <li @click="toggleSubMenu($event, 'tutorials')" class="submenu">
                        <a href="#">
                            <i class="fas fa-flag-checkered" aria-hidden="true"></i> 
                            <span>Tutorials</span> 
                            <i v-if="!toggleTutorial" class="fas fa-chevron-right position-right"></i>
                            <i v-if="toggleTutorial" class="fas fa-chevron-down position-right"></i>
                        </a>
                        <ul class="list-unstyled" :class="{ 'collapsed': !toggleTutorial, 'expanded': toggleTutorial }">
                            <li :class="{ active: isTutorialActive }"><router-link to="/tutorials"><i class="fas fa-level-up-alt icon-rotate"></i>&nbsp;&nbsp;Tutorials</router-link></li>
                        </ul>
                    </li>
                </ul>
            </div>
        </div><div class="slimScrollBar" style="background: rgb(204, 204, 204) none repeat scroll 0% 0%; width: 7px; position: absolute; top: 0px; opacity: 0.4; display: none; border-radius: 7px; z-index: 99; right: 1px; height: 561.868px;"></div><div class="slimScrollRail" style="width: 7px; height: 100%; position: absolute; top: 0px; display: none; border-radius: 7px; background: rgb(51, 51, 51) none repeat scroll 0% 0%; opacity: 0.2; z-index: 90; right: 1px;"></div></div>
    </div>
</template>

<script>

    import { Route } from 'vue-router'

    export default {
        name: 'sidebar',
        data() {
            return {
                toggleUsers: false,
                toggleTutors: false,
                toggleTutorial: false,
                activeMenu: 'dashboard',
                activeMenu: '',
                route: window.location.hash,
            }
        },

        computed: {
            isDashboardActive() {
                return this.$route.name == 'admin-dashboard'
            },

            isUserActive() {
                return this.$route.name == 'users'
            },

            isTutorActive() {
                return this.$route.name == 'tutors'
            },

            isTutorialActive() {
                return this.$route.name == 'tutorials'
            }
        },

        methods: {
            toggleSubMenu(e, target) {
                if (e.target.tagName === "SPAN") {
                    if (e.target.parentNode.parentNode.classList.contains("submenu")) {
                        if (target === 'users') {
                            this.toggleUsers = !this.toggleUsers
                        } else if (target === 'tutors') {
                            this.toggleTutors = !this.toggleTutors
                        } else if (target === 'tutorials') {
                            this.toggleTutorial = !this.toggleTutorial
                        }
                    }
                }       
            }
        },

        watch: { '$route': route => console.log(window.location.href) }
    }
</script>

<style scoped>
    .sidebar{top:60px;width:220px;z-index:1041;background-color: #1a1a1a;bottom:0;margin-top:0px;position:fixed;left:0;-webkit-transition: all 0.4s ease; -moz-transition: all 0.4s ease; transition: all 0.4s ease;border-right: 1px solid #2d2d2d;}
    .sidebar-inner{height:100%;}
    .sidebar-menu ul {font-size: 14px;list-style-type: none;margin: 0;padding: 0;position:relative;}
    .sidebar-menu li a {color: #b6b3b3;display: block;font-size: 14px;height: auto;line-height: 40px;min-height: 40px;padding: 0 20px;}
    .sidebar-menu li a:hover {color:#00bf6f;}
    .sidebar-menu li a > .badge {margin-top: 10px;color: #fff;font-weight: 700;}
    .sidebar-menu li a i {font-size: 16px;margin-left: 10px;margin-right: 10px;}
    .sidebar-menu li a i.back-icon {margin-left:0;}
    .sidebar-menu li.active a {color: #00bf6f;background-color: transparent;}
    .menu-title {color: #fff;font-size: 13px;font-weight: 500;line-height: 40px;min-height: 40px;padding: 0 20px;text-transform: uppercase;}
    .menu-title > i {float: right;line-height: 40px;}
    .sidebar-menu li.menu-title a {color: #00bf6f;float: right;padding: 0;}
    .sidebar-menu li.menu-title a.add-user-icon {background-color: #444;border-radius: 24px;color: #bbb;line-height: 24px;min-height: 24px;width: 24px;text-align: center;margin-top: 6px;}
    .sidebar-menu li.menu-title a.add-user-icon:hover {background-color: #00bf6f;color:#fff;}
    .sidebar-menu li.menu-title a.add-user-icon i {font-size: 14px;margin-left: 0;margin-right: 0;}
    .sidebar-menu li.menu-title a.btn {color: #fff;display: block;float: none;font-size: 15px;line-height: inherit;margin-bottom: 15px;}
    .sidebar-menu li.menu-title a.btn:hover {color:#fff;}
    .sidebar-menu ul ul a.active {color: #00bf6f;text-decoration: underline;}
    .mobile-user-menu {color: #fff;display: none;font-size: 24px;height: 60px;line-height: 60px;padding: 0 20px;position: absolute;right: 0;text-align: right;top: 0;width: 60px;z-index: 10;}
    .mobile-user-menu > a {color:#5c5c5c;padding:0;}
    .mobile-user-menu > a:hover {color:#333;}
    .mobile-user-menu > .dropdown-menu > a {line-height:23px;}
    .profile-rightbar {display:none !important;color: #00bf6f;font-size: 26px;margin-left: 15px;}
    .mobile_btn {display:none;}
    #sidebar-menu ul ul a {display: block;padding: 0 20px 0 60px;}
    #sidebar-menu ul ul ul a {padding-left: 80px;}
    #sidebar-menu ul ul ul ul a {padding-left: 100px;}
    #sidebar-menu ul ul {background-color: #242424;}
    .sidebar-menu > ul > li {position: relative;}
    /* .sidebar-menu .menu-arrow {} */
    .sidebar-menu .menu-arrow:before {content: "\f105";}
    .sidebar-menu li a.subdrop .menu-arrow {-ms-transform: rotate(90deg); -webkit-transform: rotate(90deg); -o-transform: rotate(90deg); transform: rotate(90deg);}
    .collapsed {display: none}
    .expanded {display: block}

    .icon-rotate {
        -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        -o-transform: rotate(90deg);
        transform: rotate(90deg);
    }

    .position-right {
        position: absolute;
        right: 20px;
        display: inline-block;
        line-height: 40px;
        font-size: 18px;
        /* text-rendering: auto; */
    }
</style>


