<template>
    <div class="admin-dashbord">
        <div class="page-wrapper" style="min-height: 747px;">
             <div class="content container-fluid">
                <div class="row">
                    <div class="col-sm-12">
                        <h4 class="page-title">Application Tutor's Records</h4>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-12">
                        <div class="card-box">
                            <div class="card-block">
                                <div id="DataTables_Table_0_wrapper" class="dataTables_wrapper container-fluid dt-bootstrap4 no-footer">
                                    <div class="row">
                                        <div class="col-sm-12 col-md-6">
                                            <div class="dataTables_length" id="DataTables_Table_0_length">
                                                <label style="display: flex; align-items: center; justify-content:">
                                                    Show&nbsp;&nbsp;
                                                    <select @change="updatePagination" v-model="per" name="DataTables_Table_0_length" aria-controls="DataTables_Table_0" class="form-control form-control" style="width: 100px; margin-top: 20px;">
                                                        <option value="10">10</option>
                                                        <option value="25">25</option>
                                                        <option value="50">50</option>
                                                        <option value="100">100</option>
                                                    </select> 
                                                    &nbsp;&nbsp;entries
                                                </label>
                                            </div>
                                        </div>
                                        <div class="col-sm-12 col-md-6 d-flex justify-content-end">
                                            <form>
                                                <div class="form-row align-items-center">
                                                    <div class="col-auto my-1">
                                                    <label class="mr-sm-2 h4">Filter By</label>
                                                    <select @change="updatePagination" class="custom-select mr-sm-2" v-model="filterBy">
                                                        <option value="0">All</option>
                                                        <option v-for="status in approvalStatus" :key="status.status_id" :value="status.status_id">{{status.name}}</option>
                                                    </select>
                                                    </div>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-sm-12">
                                            <table class="datatable table table-stripped dataTable no-footer" id="DataTables_Table_0" role="grid" aria-describedby="DataTables_Table_0_info">
                                                <thead>
                                                    <tr role="row" class="">
                                                        <th class="sorting_desc text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 40px;" aria-label="Name: activate to sort column ascending" aria-sort="descending">
                                                            #
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 19%;" aria-label="Position: activate to sort column ascending">
                                                            First Name
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 19%;" aria-label="Office: activate to sort column ascending">
                                                            Last Name
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 19%;" aria-label="Age: activate to sort column ascending">
                                                            Email
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 13%;" aria-label="Start date: activate to sort column ascending">
                                                            Mobile
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 13%;" aria-label="Age: activate to sort column ascending">
                                                            Account Status
                                                        </th>
                                                        <th class="sorting text-secondary" tabindex="0" aria-controls="DataTables_Table_0" rowspan="1" colspan="1" style="width: 13%;" aria-label="Start date: activate to sort column ascending">
                                                            Actions
                                                        </th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr v-for="(user, index) in pages[current-1]" :key="user.id" role="row" class="odd">
                                                        <td class="sorting_1 text-light">{{1+index}}</td>
                                                        <td class="text-light text-capitalize">{{ user.firstname }}</td>
                                                        <td class="text-light text-capitalize">{{ user.lastname }}</td>
                                                        <td  class="text-light text-lowercase">{{ user.email }}</td>
                                                        <td  class="text-light">{{ user.mobile }}</td>
                                                        <td  class="text-light">
                                                            <span v-if="user.approval_status === 3" class="badge badge-secondary text-capitalize">{{getStatusName(user.approval_status)}}</span>
                                                            <span v-if="user.approval_status === 1" class="badge badge-success text-capitalize">{{getStatusName(user.approval_status)}}</span>
                                                            <span v-if="user.approval_status === 4" class="badge badge-danger text-capitalize">{{getStatusName(user.approval_status)}}</span>
                                                            <span v-if="user.approval_status === 2" class="badge badge-warning text-capitalize">{{getStatusName(user.approval_status)}}</span>
                                                        </td>
                                                        <td  class="text-light">
                                                            <router-link class="btn btn-sm btn-info" style="height: 30px;" 
                                                                :to="{
                                                                    name: 'review-tutor',
                                                                    params: {user_id: user.user_id}
                                                                }"
                                                            >
                                                                Review
                                                            </router-link></td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col-sm-12 col-md-5">
                                            <div class="dataTables_info" id="DataTables_Table_0_info" role="status" aria-live="polite">
                                                <!-- Showing 1 to 10 of 57 entries -->
                                            </div>
                                        </div>
                                    <div class="col-sm-12 col-md-7">
                                    <div class="dataTables_paginate paging_simple_numbers" id="DataTables_Table_0_paginate">
                                        <ul class="pagination de-flex justify-content-end">
                                           <li :class="{'disabled': prevDisabled }" class="paginate_button page-item previous" id="DataTables_Table_0_previous">
                                                <a href="#" @click="prevNextUpdate(-1)" aria-controls="DataTables_Table_0" data-dt-idx="7" tabindex="0" class="page-link">Previous</a>
                                            </li>
                                            <li class="paginate_button page-item">
                                                <a href="#" class="page-link"><strong>{{current}}</strong> of <strong>{{last}}</strong></a>
                                            </li>
                                            <li :class="{'disabled': nextDisabled }" class="paginate_button page-item next" id="DataTables_Table_0_next">
                                                <a href="#" @click="prevNextUpdate(1)" aria-controls="DataTables_Table_0" data-dt-idx="7" tabindex="0" class="page-link">Next</a>
                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

    import datastore from '../datastore/firebaseInit'
     import {generatePagination} from '../pagination/pagination'

    import Header from './Header'
    import SideBar from './SideBar'

    export default {
        name: "admin-dashbord",
        components: {
            'dashboard-header': Header,
            'dashboard-sidebar': SideBar
        },
        data() {
            return {
                activePage: 'Dashboard',
                per: 10,
                first: null,
                last: null,
                pages: [],
                current: null,
                previous: null,
                next: null,
                appTutors: [],
                approvalStatus: [],
                filterBy: 0
            }
        },

        computed: {
            prevDisabled() {
                return this.current <= this.first;
            }, 

            nextDisabled() {
                return this.current >= this.last;
            },
        },

        created() {
            let retrieveAppUsers = new Promise((resolve, reject) => {
                datastore.collection("app_tutors").orderBy("firstname").get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        const data = {
                            "id": doc.id,
                            "firstname":  doc.data().firstname,
                            "lastname": doc.data().lastname,
                            "email": doc.data().email,
                            "mobile": doc.data().mobile,
                            "approval_status": doc.data().approval_status,
                            "user_id": doc.data().user_id
                        }
                        this.appTutors.push(data)
                    })
                })   


                datastore.collection("account_status").orderBy("name").get().then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        const data = {
                            "id": doc.id,
                            "name":  doc.data().name,
                            "status_id": doc.data().status_id
                        }
                        this.approvalStatus.push(data)
                    })
                    resolve(true)
                })  
            })

            let emitEvents = new Promise(async  (resolve, reject) => {
                await retrieveAppUsers;
                this.updatePagination();
                resolve(true)
            })
        },

        mounted() {
            // console.log(this.paginationData)
        },

        methods: {
            updatePagination() {
                let paginationData = null
            
                if (this.filterBy == 1) {
                    paginationData = generatePagination(this.filterAppTutors(this.appTutors, 1), this.per)
                } else if (this.filterBy == 2) {
                    paginationData = generatePagination(this.filterAppTutors(this.appTutors, 2), this.per)
                } else if (this.filterBy == 3) {
                    paginationData = generatePagination(this.filterAppTutors(this.appTutors, 3), this.per)
                } else if (this.filterBy == 4) {
                    paginationData = generatePagination(this.filterAppTutors(this.appTutors, 4), this.per)
                } else {
                    paginationData = generatePagination(this.appTutors, this.per)
                }

                this.first = paginationData.first
                this.last = paginationData.last
                this.pages = paginationData.pages
                this.current = paginationData.current
                this.previous = paginationData.previous
                this.next = paginationData.next
            },

            filterAppTutors(appTutors, filterParam) {
                const selected = []

                appTutors.map((tutor) => {
                    if (tutor.approval_status === filterParam) {
                        selected.push(tutor)
                    }
                })

                return selected;
            },

            prevNextUpdate(params) {
                if (this.prevDisabled && params == -1) { return null;} else if (this.nextDisabled && params == 1) { return null;}
                alert("test")
                this.current += params;
                this.previous += params; 
                this.next += params;
            },

            getStatusName(approval_status) {
                let status = null;
                this.approvalStatus.map( (statusInstance) => {
                    if (statusInstance.status_id === approval_status) {
                        status = statusInstance
                    }
                })

                if (status) {
                    return status.name
                }
                return "no name"
            }
        }
    }
</script>

<style scoped>
    .page-wrapper {left: 0;margin-left: 220px;padding-top: 30px;position: relative;-webkit-transition: all 0.4s ease;-moz-transition: all 0.4s ease;transition: all 0.4s ease;}
    .page-wrapper > .content{padding:30px;}

    @media only screen and (max-width: 991.98px) {
        .slide-nav .page-wrapper {left: 225px;margin-left: 0;}
        .page-wrapper {margin-left: 0;padding-left: 0;padding-right: 0;}
        .slide-nav .page-wrapper {left: 225px;margin-left: 0;}
        body {font-size:13px;}
    }

    @media only screen and (max-width: 575.98px) {
        .page-wrapper > .content {padding: 15px;}
        body {font-size:12px;}
    }
</style>


