<template>
    <div class="logo">
        <div 
            :class="{
                'cycle': regular,
                'medium-cycle': medium
            }"
        ></div>
        <div 
            :class="{
                't-junc': regular,
                'medium-t-junc': medium
            }"
        >
            T
        </div>
    </div>
</template>

<script>

    export default {
        name: "logo",
        props: {
            size: {
                type: String
            }
        },
        components: {

        },
        data() {
            return {
                
            }
        },

        computed: {
            regular() {
                if (this.size) {
                    return false
                }
                return true
            },

            medium() {
                return this.size === "medium"
            }
        },

        mounted() {
            // console.log(this.regular)
            // console.log(this.medium)
        }
    }
</script>

<style scoped>
    .logo {
        width: 100%;
        height: 40px;
        position: relative;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .logo div {
        width: 100%;
        height: 100%;
        position: absolute;

        display: flex;
        align-items: center;
        justify-content: center;
    }

    .cycle {
        height: 60px !important;
        width: 60px !important;

        background: #28a745;
        border-radius: 100%;
    }

    .medium-cycle {
        height: 100px;
        width: 100px;

        background: #28a745;
        border-radius: 100%;
    }

    .t-junc {
        font-size: 35px;
        font-weight: 900;
    }

    .medium-t-junc{
        font-size: 25px;
        font-weight: 900;
    }


    .t-junc, .medium-t-junc{
        color: #000;
        font-family: sans-serif;
    }
</style>


