<template>
    <div id="app">
      <dashboard-header class="dashbord-header" :activePage="activePage"></dashboard-header>
      <dashboard-sidebar class="dashboard-sidebar" :key="$route.fullPath"></dashboard-sidebar>
      <router-view></router-view>
    </div>
</template>

<script>

  import firebase from 'firebase'

  import Header from './components/Header'
  import SideBar from './components/SideBar'

    export default {
      name: 'App',
      components: {
        'dashboard-header': Header,
        'dashboard-sidebar': SideBar
      },

      computed: {
         key() {
            return this.$route.name 
         },

         activePage() {
            if (this.$route.name == 'admin-dashboard') {
              return "Dashboard"
            } else if (this.$route.name == 'users') {
              return "Users"
            } else if (this.$route.name == 'tutors') {
              return "Tutors"
            } else if (this.$route.name == 'tutorials') {
              return "Tutorials"
            } else if (this.$route.name == 'review-tutor') {
              return "Tutors/Review"
            }
            return "Undefined"
         }
      }
    }
</script>

<style>

</style>



