<template>
    <div class="header">
        <div class="header-left">
            <router-link class="logo" to="/">
                <logo class="logo" :size="medium" style="width: 40px; height: 40px;"></logo>
            </router-link>
        </div>
        <div class="page-title-box pull-left" style="display: flex; align-items: center; height: 100%;">
            <h3>{{ pageLabel }}{{ activePage }}</h3>
        </div>
        <ul class="nav user-menu pull-right" style="width: 100%; display: 100%; justify-content: flex-end;">
            <li class="nav-item dropdown has-arrow">
                <a href="#" class="dropdown-toggle nav-link user-link user-title" data-toggle="dropdown" style="display: flex; align-items: center; justify-content: center;">
                    <i class="fas fa-user-circle fa-2x"></i>
                    <span style="padding: 0 10px">{{name}}</span>
                </a>
                <div class="dropdown-menu">
                    <a @click="logAdminUserOut" href="#" class="dropdown-item"><i class="fas fa-sign-out-alt"></i>&nbsp;&nbsp;Logout</a>
                </div>
            </li>
        </ul>
        
    </div>
</template>

<script>

    import firebase from 'firebase'

    import Logo from "./Logo"

    export default {
        name: 'dashboard-header',
        props: {
            activePage: {
                type: String,
                required: true
            }
        },
        components: {
            'logo': Logo
        },
        data() {
            return {
                medium: 'medium',
                pageLabel: 'Online Tutor/',
                name: "Super Administrator"
            }
        },

        methods: {
            logAdminUserOut() {
                firebase.auth().signOut().then(() => {
                    this.$router.go({ path: this.$router.path })
                })
            }
        }
    }
</script>

<style scoped>
    .header {background-color: #1a1a1a;left: 0;position: fixed;right: 0;top: 0;z-index: 1041;height: 60px;border-bottom: 1px solid #2d2d2d; position: relative;}
    .header .header-left {float: left;height: 60px;padding: 0 20px;position: relative;text-align: center;width: 220px;z-index: 1;background: #1a1a1a;}
    .logo {display: block;line-height: 60px; margin-top: 5px;}
    .header .navbar-nav .badge {position: absolute;right: 7px;top: 4px;}
    .header .dropdown-menu > li > a {position: relative;}
    .header .dropdown-toggle::after {display: none;}
    .header .has-arrow .dropdown-toggle::after {display: inline-block;}

    .header-left {display: flex; /*align-items: center;*/ justify-content: center; }

    .user-title {color: #fff;font-size: 13px;font-weight: 500;line-height: 40px;min-height: 40px;padding: 0 20px;text-transform: uppercase;}

    @media only screen and (max-width: 991.98px) {
        .header .header-left {position:absolute; width:100%;}
    }

    @media only screen and (max-width: 767.98px) {
        .header {z-index:1040;}
        .header .has-arrow .dropdown-toggle > span:nth-child(2) {display:none;}
        .header .has-arrow .dropdown-toggle::after {display:none;}
    }
</style>


